import React from 'react'

const ProjectPreview = () => (
    <>
        <div>
            <section id="portfolio" className="portfolio">
                <div className="container">
                    <h2 className="text-uppercase text-center text-secondary">Portfolio</h2>
                    <hr className="star-dark mb-5" />
                    <div className="row">
                        <div className="col-md-6 col-lg-4"><a className="d-block mx-auto portfolio-item" data-toggle="modal" href="#portfolio-modal-1">
                            <div className="d-flex portfolio-item-caption position-absolute h-100 w-100">
                                <div className="text-center text-white my-auto portfolio-item-caption-content w-100"><i className="fa fa-search-plus fa-3x" /></div>
                            </div><img className="img-fluid" src="assets/img/portfolio/cabin.png" />
                        </a></div>
                        <div className="col-md-6 col-lg-4"><a className="d-block mx-auto portfolio-item" data-toggle="modal" href="#portfolio-modal-2">
                            <div className="d-flex portfolio-item-caption position-absolute h-100 w-100">
                                <div className="text-center text-white my-auto portfolio-item-caption-content w-100"><i className="fa fa-search-plus fa-3x" /></div>
                            </div><img className="img-fluid" src="assets/img/portfolio/cake.png" />
                        </a></div>
                        <div className="col-md-6 col-lg-4"><a className="d-block mx-auto portfolio-item" data-toggle="modal" href="#portfolio-modal-3">
                            <div className="d-flex portfolio-item-caption position-absolute h-100 w-100">
                                <div className="text-center text-white my-auto portfolio-item-caption-content w-100"><i className="fa fa-search-plus fa-3x" /></div>
                            </div><img className="img-fluid" src="assets/img/portfolio/circus.png" />
                        </a></div>
                        <div className="col-md-6 col-lg-4"><a className="d-block mx-auto portfolio-item" data-toggle="modal" href="#portfolio-modal-4">
                            <div className="d-flex portfolio-item-caption position-absolute h-100 w-100">
                                <div className="text-center text-white my-auto portfolio-item-caption-content w-100"><i className="fa fa-search-plus fa-3x" /></div>
                            </div><img className="img-fluid" src="assets/img/portfolio/game.png" />
                        </a></div>
                        <div className="col-md-6 col-lg-4"><a className="d-block mx-auto portfolio-item" data-toggle="modal" href="#portfolio-modal-5">
                            <div className="d-flex portfolio-item-caption position-absolute h-100 w-100">
                                <div className="text-center text-white my-auto portfolio-item-caption-content w-100"><i className="fa fa-search-plus fa-3x" /></div>
                            </div><img className="img-fluid" src="assets/img/portfolio/safe.png" />
                        </a></div>
                        <div className="col-md-6 col-lg-4"><a className="d-block mx-auto portfolio-item" data-toggle="modal" href="#portfolio-modal-6">
                            <div className="d-flex portfolio-item-caption position-absolute h-100 w-100">
                                <div className="text-center text-white my-auto portfolio-item-caption-content w-100"><i className="fa fa-search-plus fa-3x" /></div>
                            </div><img className="img-fluid" src="assets/img/portfolio/submarine.png" />
                        </a></div>

                    </div>
                    <div className="form-group"><button className="btn btn-primary btn-xl" id="viewProjectsButton" type="submit">See all the projects</button></div>
                </div>
            </section>
            <section className="text-white bg-primary mb-0" id="about">
                <div className="container">
                    <h2 className="text-uppercase text-center text-white">About</h2>
                    <hr className="star-light mb-5" />
                    <div className="row">
                        <div className="col-lg-4 ml-auto">
                            <p className="lead">Pariatur aliquip occaecat excepteur do occaecat dolore velit cillum aliqua esse deserunt excepteur. Incididunt quis adipisicing est dolor sit magna quis aute irure irure amet magna. Dolore dolore sint cupidatat consequat proident velit tempor enim do. Ea nisi Lorem nulla esse ullamco incididunt sunt consectetur ut elit qui officia.</p>
                        </div>
                        <div className="col-lg-4 mr-auto">
                            <p className="lead">Aliquip id ut aliquip fugiat elit sint anim occaecat pariatur commodo ea cillum. Qui mollit nisi ipsum quis minim incididunt est nisi dolore exercitation pariatur esse dolore excepteur. Aliqua commodo tempor culpa consequat voluptate tempor aliqua in commodo adipisicing aliquip in elit. Ex deserunt est ex ullamco officia non qui aute reprehenderit.</p>
                        </div>
                    </div>
                </div>

            </section>
            <section id="contact">
                <div className="container">
                    <h2 className="text-uppercase text-center text-secondary mb-0">Contact Me (Under construction)</h2>
                    <hr className="star-dark mb-5" />
                    <div className="row">
                        <div className="col-lg-8 mx-auto">
                            <form id="contactForm" name="sentMessage" noValidate="novalidate">
                                <div className="control-group">
                                    <div className="form-group floating-label-form-group controls mb-0 pb-2"><label>Name</label><input className="form-control" type="text" id="name" required placeholder="Name" /><small className="form-text text-danger help-block" /></div>
                                </div>
                                <div className="control-group">
                                    <div className="form-group floating-label-form-group controls mb-0 pb-2"><label>Email Address</label><input className="form-control" type="email" id="email" required placeholder="Email Address" /><small className="form-text text-danger help-block" /></div>
                                </div>
                                <div className="control-group">
                                    <div className="form-group floating-label-form-group controls mb-0 pb-2"><label>Phone Number</label><input className="form-control" type="tel" id="phone" required placeholder="Phone Number" /><small className="form-text text-danger help-block" /></div>
                                </div>
                                <div className="control-group">
                                    <div className="form-group floating-label-form-group controls mb-5 pb-2"><textarea className="form-control" id="message" required placeholder="Message" rows={5} defaultValue={""} /><small className="form-text text-danger help-block" /></div>
                                </div>
                                <div id="success" />
                                <div className="form-group"><button className="btn btn-primary btn-xl" id="sendMessageButton" type="submit">Send</button></div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </div>

    </>
)

export default ProjectPreview;